import { NavLink } from "react-router-dom";
import { Header } from './Header';

export function Register({loggedIn, login}) {

    return (
        <>
            <Header loggedIn={loggedIn} login={login} placeholder='Вход'></Header>
            <section className='auth'>
                <h2 className='auth__header'>Регистрация</h2>
                <form className='auth__form'>
                    <input className='auth__input' placeholder='E-mail'></input>
                    <input className='auth__input' placeholder='Пароль'></input>
                    <button className='auth__submit'>Зарегистрироваться</button>
                    <NavLink to="/sign-in" className="auth__link">Уже зарегистрированы? Войти</NavLink>
                </form>
            </section>
        </>
    )
}