import { NavLink } from "react-router-dom";
import { Header } from './Header';

export function Login({loggedIn, login}) {

    return (
        <>
            <Header loggedIn={loggedIn} login={login} placeholder='Регистрация'></Header>
            <section className='auth'>
                <h2 className='auth__header'>Вход</h2>
                <form className='auth__form'>
                    <input className='auth__input' placeholder='E-mail'></input>
                    <input className='auth__input' placeholder='Пароль'></input>
                    <button className='auth__submit'>Войти</button>
                </form>
            </section>
        </>
    )
}